import {
  Table,
  TableHeader,
  TableHeaderCell,
  TableBody,
  TableRow,
  TableCell,
  Label,
  Rating,
  Header,
  HeaderContent,
  HeaderSubheader,
  Button,
  Icon,
} from "semantic-ui-react";

export default function DataTable() {
  const data = [
    {
      name: "",
    },
    {
      name: "",
    },
    {
      name: "",
    },
    {
      name: "",
    },
  ];
  return (
    <>
      <Table>
        <TableHeader>
          <TableHeaderCell></TableHeaderCell>
        </TableHeader>
        <TableBody>
          {data.map((obj) => (
            <TableRow>
              <TableCell>{obj.name}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  );
}
